import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { QuickPaymentModule } from '../public-api';
import { BillingModule } from './pages/billing/billing.module';
import { CheckoutModule } from './pages/checkout/checkout.module';
import { ConfirmationModule } from './pages/confirmation/confirmation.module';
import { DefaultModule } from './pages/default/default.module';
import { FinalizePaymentModule } from './pages/finalize-payment/finalize-payment.module';
import { PagesModule } from './pages/pages.module';
import { PickUpLocationSettingModule } from './pages/pick-up-location-setting/pick-up-location-setting.module';
import { PreAuthorizedPaymentModule } from './pages/pre-authorized-payment/pre-authorized-payment.module';
import { SettingsModule } from './pages/settings/settings.module';
import { ShippingZoneModule } from './pages/shipping-zone/shipping-zone.module';
import { PaymentEngineRoutingModule } from './payment-engine-routing.module';
import { PaymentEngineComponent } from './payment-engine.component';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MODULES = [
  PagesModule,
  PaymentEngineRoutingModule,
  PreAuthorizedPaymentModule,
  CheckoutModule,
  BillingModule,
  SettingsModule,
  ConfirmationModule,
  FinalizePaymentModule,
  ShippingZoneModule,
  PickUpLocationSettingModule,
  QuickPaymentModule,
  DefaultModule
];

const COMPONENTS = [PaymentEngineComponent];
const PIPES = [];

@NgModule({
  imports: [...BASE_MODULES, ...MODULES],
  exports: [...BASE_MODULES, ...MODULES, ...COMPONENTS, ...PIPES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: []
})
export class PaymentEngineModule { }
