import { NgModule } from '@angular/core';
import { ConfirmationComponent } from './confirmation.component';
import { ConfirmationService } from '../../@services/confirmation.service';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { CommonModule } from '@angular/common';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { LoadingModule } from '@tei/material';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TeiDateTranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatIconModule, MatExpansionModule, MatDividerModule, MatCardModule, MatChipsModule];
const TEI_MATERIAL_MODULES = [LoadingModule];

@NgModule({
  declarations: [ConfirmationComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: ConfirmationComponent }]),
    ...BASE_MODULES,
	...MATERIAL_MODULES,
	...TEI_MATERIAL_MODULES,
  ...CORE_MODULES
  ],
  providers:[{provide: 'IConfirmationService', useClass: ConfirmationService}],
})
export class ConfirmationModule { }
