export class GetShippingZoneRequest
{
  public ZoneID: string;
}

export class GetShippingZoneResponse
{
  public ShippingProperties: Array<ShippingProperty>;
  public Regions: Array<SearchWithChipDatasource>;
  public ShippingZone: ShippingZone;
}

export class ShippingZone
{
  public ZoneID: string;
  public ZoneName: string;
  public SelectedRegion: Array<string>;
  public Rates: Array<ShippingRate>;
}

export class ShippingRate
{
  public RateID: string;
  public Description: string;
  public Fee: string;
  public IsOption: boolean;
  public IsDeleted: boolean;
  public ShippingPropertyValues: Array<ShippingPropertyValue>;
  public RateIndex: number;
}

export class ShippingPropertyValue
{
  public PropertyID: number;
  public PropertyName: string;
  public PropertyValue: string;
}

export class ShippingProperty
{
  public PropertyID: number;
  public PropertyName: string;
  public PropertyDescription: string;
}



export class SearchWithChipDatasource
{
  public GroupName: string;
  public List: Array<SearchWithChipDataList>;
}

export class SearchWithChipDataList
{
  public Display: string;
  public Value: string;
}
