import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { CartItemsResponse, CartItemsRequest, IShoppingCartService } from '../@intermediate/shopping-cart';



@Injectable({
  providedIn: 'root'
})

export class ShoppingCartService implements IShoppingCartService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/ShoppingCart';
  }


  public Post(request: CartItemsRequest): Observable<CartItemsResponse>
  {
    const apiUrl = this.serviceUrl;
    return this.http.post<CartItemsResponse>(apiUrl, request);
  }
}
