import { Component, Inject, OnInit, SecurityContext } from '@angular/core';
import { MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'tei-payment-engine-billing-terms-conditions-dialog',
  templateUrl: './billing-terms-conditions-dialog.component.html',
  styleUrls: ['./billing-terms-conditions-dialog.component.scss']
})
export class BillingTermsConditionsDialogComponent implements OnInit
{

  sanitizedTermsConditions: string;
  constructor(
    private dialogRef: MatDialogRef<BillingTermsConditionsDialogComponent>,
    private domSanitizer: DomSanitizer,
    @Inject(MAT_DIALOG_DATA) public dialogData: { details: string; }
  ) 
  {
  }

  ngOnInit(): void
  {
    this.sanitizedTermsConditions = this.domSanitizer.sanitize(SecurityContext.NONE, this.dialogData.details);
  }

  onConfirm(): void
  {
    this.dialogRef.close(true);
  }

  onDecline(): void
  {
    this.dialogRef.close(false);
  }

  close(): void
  {
    this.dialogRef.close();
  }
}
