import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IConfirmationService, GetConfirmationRequest, GetConfirmationResponse, PostConfirmationRequest, PostConfirmationResponse } from '../@intermediate/confirmation';

@Injectable({
  providedIn: 'root'
})

export class ConfirmationService implements IConfirmationService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/Confirmation';
  }

  public Get(request: GetConfirmationRequest): Observable<GetConfirmationResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetConfirmationResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostConfirmationRequest): Observable<PostConfirmationResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostConfirmationResponse>(apiUrl, request);
  }
}
