import { UntypedFormGroup, UntypedFormControl, UntypedFormArray, Validators } from '@angular/forms';
import { GetShippingZoneResponse, ShippingRate, ShippingPropertyValue, ShippingZone } from './get-shipping-zone.model';


export class PostShippingZoneRequest
{
  Rates: ShippingRate[];
  SelectedRegion: Array<any>;
  ZoneName: string;
  ZoneID: string;
}

export class PostShippingZoneResponse
{
  Zone: GetShippingZoneResponse;
}


export class PostShippingZoneRequestForm extends UntypedFormGroup
{
  public readonly Rates: ShippingRateFormArrray;

  constructor(value: ShippingZone[])
  {
    super({
      SelectedRegion: new UntypedFormControl([], Validators.required),
      ZoneName: new UntypedFormControl('', Validators.required),
      Rates: new ShippingRateFormArrray(value),
    });
    this.Rates = this.get('Rates') as ShippingRateFormArrray;
  }

  public value: PostShippingZoneRequest;

  public setValue(value: GetShippingZoneResponse)
  {
    const request = new PostShippingZoneRequest();
    request.Rates = value?.ShippingZone?.Rates ? value.ShippingZone?.Rates : [];
    request.SelectedRegion = value?.ShippingZone?.SelectedRegion ? value.ShippingZone?.SelectedRegion : [];
    request.ZoneName = value?.ShippingZone?.ZoneName ? value.ShippingZone?.ZoneName : "";
    super.setValue(request);
  }

}

export class ShippingRateForm extends UntypedFormGroup
{
  public readonly ShippingPropertyValues: ShippingPropertyValuesArray;

  constructor(value: ShippingRate)
  {
    super({
      Description: new UntypedFormControl(value.Description, [descriptionValidator]),
      Fee: new UntypedFormControl(value.Fee, Validators.required),
      IsDeleted: new UntypedFormControl(value.IsDeleted),
      IsOption: new UntypedFormControl(value.IsOption),
      RateID: new UntypedFormControl(value.RateID),
      RateIndex: new UntypedFormControl(value.RateIndex),
      ShippingPropertyValues: new ShippingPropertyValuesArray(value.ShippingPropertyValues)
    });
    this.ShippingPropertyValues = this.get('ShippingPropertyValues') as ShippingPropertyValuesArray;
  }

  public value: PostShippingZoneRequest;

  public setValue(value: GetShippingZoneResponse)
  {
    const request = new PostShippingZoneRequest();
    super.setValue(request);
  }

}


export class ShippingRateFormArrray extends UntypedFormArray
{
  constructor(value: ShippingZone[])
  {
    super([]);
  }
  public setValue(value: ShippingRate[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(rate =>
      {
        let shippingRateForm = new ShippingRateForm(rate);
        this.push(shippingRateForm);
      });
    }
  }
}



export class ShippingPropertyValuesForm extends UntypedFormGroup
{
  public readonly ShippingRate: ShippingRateFormArrray;

  constructor(value: ShippingPropertyValue)
  {
    super({
      PropertyID: new UntypedFormControl(value.PropertyID),
      PropertyName: new UntypedFormControl(value.PropertyName),
      PropertyValue: new UntypedFormControl(value?.PropertyValue, [rangeValidator]),
    });
  }
  public value: PostShippingZoneRequest;

  public setValue(value: GetShippingZoneResponse)
  {
    const request = new PostShippingZoneRequest();
    super.setValue(request);
  }

}


export function descriptionValidator(control: UntypedFormGroup): { [key: string]: string; } | null
{
  let validDescription = true;
  if (!tempArrayIsDeleted)
  {
    if (control?.parent?.controls["Description"]._parent._parent.value.length > 1)
    {
      control?.parent?.controls["Description"]._parent._parent.value.forEach(element =>
      {
        if (control.value == element.Description && element.IsOption == true && control.value != "")
        {
          validDescription = false;
          return;
        }
      });

      if (validDescription)
      {
        return null;
      }
      else
      {
        return { "descriptionValidator": "Name already exist." };
      }
    }
  }
}


let tempArray = [];
let tempArrayIsDeleted: boolean = false;
export function rangeValidator(control: UntypedFormGroup): { [key: string]: string; } | null
{

  let t: boolean = false;
  let controlPropertyIDValue = control["_parent"]?.controls?.PropertyID?.value;
  let currentIndex = control?.parent?.controls["PropertyValue"]?._parent?._parent?._parent?.value?.RateIndex;
  let temparr = [];
  let minCartValidation: boolean = false;
  let currentControlInvalid: boolean = false;

  if (!tempArrayIsDeleted)
  {
    const arr = tempArray.filter(x => x.value.length > 0);
    let rangeArray = [];
    let rangeArray1 = [];

    control?.parent?.controls["PropertyValue"]._parent._parent._parent._parent.value.forEach(element =>
    {
      if (element.IsOption == false && element.IsDeleted == false)
      {
        if (currentIndex == element.RateIndex)
        {
          element.ShippingPropertyValues.forEach(x =>
          {
            if (x.PropertyID == controlPropertyIDValue)
            {
              x.PropertyValue = control.value;
            }
          });
        }
        temparr.push(element.ShippingPropertyValues);
      }
    });


    temparr.forEach((c, v) =>
    {
      c.forEach((element, i) =>
      {
        if (v < temparr.length - 1)
        {
          rangeArray.push(element.PropertyValue);
        }
      });
    });

    if (temparr.length > 0)
    {
      rangeArray1.push(temparr[temparr.length - 1]);
    }

    temparr.forEach((value, index) =>
    {
      if (temparr.length > 0)
      {
        let min = rangeArray1[0][0].PropertyValue;
        let max = rangeArray1[0][1].PropertyValue;
        if (max == null)
        {
          max = -1;
        }

        value.forEach((x, i) =>
        {
          if ((value[0].PropertyValue > value[1].PropertyValue) && (value[0].PropertyValue != null && value[1].PropertyValue != null) && currentIndex == (index + 1))
          {
            minCartValidation = true;
            currentControlInvalid = true;
            return;
          }

          if (control.value >= value[0].PropertyValue && control.value <= value[1].PropertyValue && value[1].PropertyValue != null && currentIndex != (index + 1) && control.value != null)
          {
            t = true;
            currentControlInvalid = true;
            return;
          }
        });
        rangeArray.forEach(element =>
        {
          if (element >= min && min != null && (element <= max && max != -1) && addNewRate == false)
          {
            t = true;
            currentControlInvalid = true;
            return;
          }
          if (max == -1 && min <= element && min != null)
          {
            t = true;
            currentControlInvalid = true;
            return;
          }
        });

        if (addNewRate == false && ((max != -1 && max > min) || (max == -1 && min != null)) && currentControlInvalid == false)
        {
          t = false;
          return;
        }
        if (addNewRate == false && max != -1 && min < max && currentControlInvalid == false)
        {
          t = false;
          return;
        }


      }
    });

    if (!t && currentControlInvalid == false)
    {
      if (control.status === "INVALID")
      {
        control?.setErrors(null);
      }
      return null;
    }
    else
    {
      if (minCartValidation)
      {
        return { 'rateRangeValidator': "Min cart is greater than Max cart." };
      }
      else if (t)
      {
        return { 'rateRangeValidator': "This range already exists" };
      }
      else
      {
        return null;
      }
    }
  }
}

export function removeTempArray(index: number)
{
  let arr = tempArray;
  tempArray = [];
  tempArray = arr.filter((x, i) => i != index);
  tempArrayIsDeleted = true;
}

export function setRemoveTempArray(setValue: boolean)
{
  tempArrayIsDeleted = setValue;
}

export class ShippingPropertyValuesArray extends UntypedFormArray
{
  constructor(value: ShippingPropertyValue[])
  {
    super([]);
    this.setValue(value);
  }

  public setValue(value: ShippingPropertyValue[],
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    if (value != undefined)
    {
      value.forEach(rate =>
      {
        let shippingRateForm = new ShippingPropertyValuesForm(rate);
        this.push(shippingRateForm);
      });
    }
    if (addNewRate)
    {
      addNewRate = false;
      tempArray.push(this);
    }
    else
    {
      let filteredValue = this.value.filter(element => element.PropertyID == 1 && (element.PropertyValue != "" && element.PropertyValue != null));
      if (filteredValue.length > 0)
      {
        addNewRate = false;
        tempArray.push(this);

      }
    }
  }
}

let addNewRate: boolean = false;
export function isAddRate(addRate: boolean)
{
  addNewRate = true;
}
