import { Component, OnInit, Inject, ElementRef, ViewChildren } from '@angular/core';
import { IShippingZoneService, GetShippingZoneRequest, GetShippingZoneResponse, PostShippingZoneRequestForm, ShippingZone, ShippingRate, ShippingPropertyValue, PostShippingZoneRequest, rangeValidator, ShippingPropertyValuesArray, ShippingRateFormArrray, removeTempArray, setRemoveTempArray, isAddRate } from '../../@intermediate/shipping-zone';
import { UntypedFormArray, FormBuilder, Validators } from '@angular/forms';
import { SnackBarService, DialogData, ConfirmDialogService, ChipDataRequest } from '@tei/material';
import { ActivatedRoute, Router } from '@angular/router';
import { DeleteShippingZoneRequest } from '../../@intermediate/shipping-zone/models/delete-shipping-zone.model';
import { IAPIBaseUrlProvider } from '@tei/core';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'tei-payment-engine-pages-shipping-zone',
  templateUrl: './shipping-zone.component.html',
  styleUrls: ['./shipping-zone.component.scss']
})
export class ShippingZoneComponent implements OnInit
{
  model: GetShippingZoneResponse;
  form: PostShippingZoneRequestForm;
  panelOpenState: any;
  placeHolder: any;
  shippingZone: ShippingZone[];
  regions: any[];
  selectedRegions: any[];
  isRemoved: boolean = false;
  zoneID: string = '';
  deleteToggle: boolean = false;
  shippingFeeMessage: string = "";
  addRateDisabled: boolean = false;
  hideFreeShippingAlert: boolean = false;

  constructor(@Inject('IShippingZoneService') private ShippingZoneService: IShippingZoneService,
    activatedRoute: ActivatedRoute,
    private router: Router,
    private translateService: TranslateService,
    private snackbarService: SnackBarService,
    private confirmdialogService: ConfirmDialogService,
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider
  )
  {
    activatedRoute.params.subscribe(params =>
    {
      if (params['zone-id'])
      {
        this.zoneID = params['zone-id'];
        this.deleteToggle = true;
      }
    });
  }

  ngOnInit()
  {
    this.form = new PostShippingZoneRequestForm(undefined);
    const request = new GetShippingZoneRequest();
    request.ZoneID = this.zoneID;
    this.load(request);
    let range = [];
    this.form.valueChanges.subscribe(value =>
    {
      if (value.ZoneName)
      {
        if (this.model.ShippingZone == null)
        {
          this.model.ShippingZone = new ShippingZone();
        }
        this.model.ShippingZone.ZoneName = value.ZoneName;
      }

      if (value.Rates)
      {
        this.calculateFreeShippingPrice(value.Rates);
      }

      var rindex = 1;
      value.Rates.forEach((x, i) =>
      {
        if (x.IsOption == true && x.IsDeleted == false)
        {
          x.RateIndex = rindex++;
        }

      });
      var oindex = 1;
      value.Rates.forEach((x, i) =>
      {
        if (x.IsOption == false && x.IsDeleted == false)
        {
          x.RateIndex = oindex++;
        }

      });

      // re- validate the rate range
      if (this.form.Rates.status === "INVALID")
      {
        this.form.Rates.controls.forEach(x =>
        {
          if (x["controls"].ShippingPropertyValues.status === "INVALID")
          {
            x["controls"].ShippingPropertyValues.controls.forEach(control =>
            {
              control["_parent"].controls.forEach(c =>
              {
                rangeValidator(c.controls.PropertyValue);
              });

            });
          }
        });
      }
      setRemoveTempArray(false);
    });
  }

  calculateFreeShippingPrice(value: any)
  {
    if (value.filter(f => f.IsOption == false && f.IsDeleted == false).length == 0)
    {
      this.shippingFeeMessage = "All orders are treated as free shipping. Please add rates to charge fees based on cart value.";
      this.addRateDisabled = false;
    }
    else
    {
      const rate = value.filter(x => x.IsOption === false && x.IsDeleted == false);
      let arr = [];
      let maxFeeInfinity: boolean = false;
      rate.forEach(element =>
      {
        if (element.ShippingPropertyValues[0].PropertyValue != null && (element.ShippingPropertyValues[1].PropertyValue == null || element.ShippingPropertyValues[1].PropertyValue == ""))
        {
          maxFeeInfinity = true;
          return;
        }
        let maxValue = Math.max.apply(Math, element.ShippingPropertyValues.map(function (o) { return o.PropertyValue; }));
        arr.push(maxValue);
      });

      if (!maxFeeInfinity)
      {
        this.hideFreeShippingAlert = false;
        let maxRate = Math.max.apply(Math, arr.map(function (o) { return o; }));
        this.shippingFeeMessage = `Free shipping on orders over $${ maxRate.toFixed(2) }`;
        this.addRateDisabled = false;
      }
      else
      {
        this.hideFreeShippingAlert = true;
        this.shippingFeeMessage = "";
        this.addRateDisabled = true;
      }

      rate.forEach(element =>
      {
        element.ShippingPropertyValues.forEach(e =>
        {
          if (e.PropertyID == 1 && e.PropertyValue == null || e.PropertyID == 2 && e.PropertyValue == null)
          {
            this.addRateDisabled = true;
            return;
          }

        });
      });
    }
  }

  private load(request: GetShippingZoneRequest)
  {
    if (!this.model)
    {
      this.model = new GetShippingZoneResponse();
    }

    this.ShippingZoneService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.regions = response.Regions;
      this.reOrderRates(this.model);
      this.selectedRegions = this.model.ShippingZone?.SelectedRegion;
      setRemoveTempArray(true);
      this.form.setValue(this.model);
      setRemoveTempArray(true);
    });
  }

  save(form: PostShippingZoneRequestForm)
  {
    const shippingZoneRequest = new PostShippingZoneRequest();

    shippingZoneRequest.Rates = form.value.Rates.filter(x => (x.IsDeleted == false && x.RateID == null) || (x.RateID != null));
    let list: Array<string>;
    list = [];
    let shippingRate: ShippingRate[];
    form.value.SelectedRegion["DisplayChip"].forEach(e =>
    {
      list.push(e.Value);
    });
    shippingZoneRequest.SelectedRegion = list;
    shippingZoneRequest.ZoneName = form.value.ZoneName;
    shippingZoneRequest.ZoneID = this.zoneID;
    this.ShippingZoneService.Post(shippingZoneRequest).subscribe(response =>
    {

      this.model = response.Zone;
      shippingRate = this.model.ShippingZone.Rates;
      setRemoveTempArray(true);
      this.reOrderRates(this.model);
      if (response.Zone.ShippingZone.ZoneID)
      {
        this.deleteToggle = true;
        this.zoneID = response.Zone.ShippingZone.ZoneID;
        setRemoveTempArray(true);
        this.snackbarService.showSuccess(this.translateService.instant('PAYMENT_ENGINE.SHIPPING_ZONE.SHIPPING_ZONE_SAVED'));
      }

      this.selectedRegions = this.model.ShippingZone?.SelectedRegion;
      if (shippingRate)
      {
        setRemoveTempArray(true);
        this.form.Rates.clear();
        this.form.setValue(this.model);
        this.calculateFreeShippingPrice(this.model.ShippingZone.Rates);
        setRemoveTempArray(true);
      }
    });


  }

  reOrderRates(model: GetShippingZoneResponse)
  {
    this.model = model;
    let optionIndex = 1;
    let valueIndex = 1;
    this.model.ShippingZone?.Rates?.forEach(o =>
    {
      if (o.IsOption == true && o.IsDeleted == false)
      {
        o.RateIndex = optionIndex;
        optionIndex++;
      }
    });

    this.model.ShippingZone?.Rates?.forEach(o =>
    {
      if (o.IsOption == false && o.IsDeleted == false)
      {
        o.RateIndex = valueIndex;
        valueIndex++;
      }
    });
  }

  addRate(isOption: boolean)
  {
    isAddRate(true);
    this.shippingFeeMessage = "";
    const shippingRateArray = this.form.get('Rates') as UntypedFormArray;
    const shippingRateProperty = new ShippingRate();

    shippingRateProperty.IsDeleted = false;
    shippingRateProperty.IsOption = isOption;
    shippingRateProperty.Description = "";
    shippingRateProperty.ShippingPropertyValues = [];
    const maxIndex = shippingRateArray.value.filter(o => o.IsOption == isOption && o.IsDeleted == false).length;
    shippingRateProperty.RateIndex = maxIndex + 1;
    this.model.ShippingProperties.forEach(prop =>
    {
      const shippingPropertyValues = new ShippingPropertyValue();
      shippingPropertyValues.PropertyName = prop.PropertyName;
      shippingPropertyValues.PropertyID = prop.PropertyID;
      shippingRateProperty.ShippingPropertyValues.push(shippingPropertyValues);
    });
    shippingRateArray.setValue([shippingRateProperty]);
  }

  removeRate(index: number, removeIsOptionIndex: number, isRate: boolean)
  {
    let shippingRate = this.form.get('Rates') as UntypedFormArray;
    const deleteMsg = isRate ? 'Are you sure you want to delete the Calculated Rate?' : 'Are you sure you want to delete the Shipping Option?';
    const dialogData: DialogData = {
      title: 'Delete',
      message: deleteMsg,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    if (shippingRate.controls.length > 0)
    {
      this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
      {
        if (result)
        {
          let isOption: number;
          let rateID: number;
          if (isRate)
          {
            shippingRate.controls.forEach((x, ci) =>
            {
              if (ci == removeIsOptionIndex && x.value.IsOption == false)
              {
                x.value.IsDeleted = true;
                isOption = x.value.IsOption;
                rateID = x.value.RateID;
                shippingRate.controls[ci]["controls"].Description.status = "VALID";
                shippingRate.controls[ci]["controls"].Fee.status = "VALID";
                shippingRate.controls[ci]["controls"].ShippingPropertyValues.status = "VALID";
                shippingRate.controls[ci]["controls"].ShippingPropertyValues["controls"][0].status = "VALID";
                shippingRate.controls[ci]["controls"].ShippingPropertyValues["controls"][1].status = "VALID";
                shippingRate.controls[ci]["controls"].ShippingPropertyValues["controls"].status = "VALID";
                shippingRate.controls[ci]["controls"].ShippingPropertyValues.controls[0]["controls"].PropertyValue.status = "VALID";
                shippingRate.controls[ci]["controls"].ShippingPropertyValues.controls[1]["controls"].PropertyValue.status = "VALID";
                shippingRate.controls[ci].setErrors(null);
                return;
              }
            });
          }
          else
          {
            shippingRate.controls.forEach((x, ci) =>
            {
              if (ci == removeIsOptionIndex && x.value.IsOption == true)
              {
                x.value.IsDeleted = true;
                isOption = x.value.IsOption;
                rateID = x.value.RateID;
                shippingRate.controls[ci]["controls"].Description.status = "VALID";
                shippingRate.controls[ci]["controls"].Fee.status = "VALID";
                shippingRate.controls[ci].setErrors(null);
                return;
              }

            });

          }

          this.calculateFreeShippingPrice(shippingRate.value.filter(o => o.IsOption == false && o.IsDeleted == false));
          if (isRate)
          {
            shippingRate.controls.forEach((element, i) =>
            {
              if (i >= removeIsOptionIndex && (element.value.IsOption == false) && element.value.IsDeleted == false)
              {
                element.value.RateIndex = (element.value.RateIndex) - 1;
              }
            });
          }
          else
          {
            shippingRate.controls.forEach((element, i) =>
            {
              if (i >= removeIsOptionIndex && (element.value.IsOption == true) && element.value.IsDeleted == false)
              {
                element.value.RateIndex = (element.value.RateIndex) - 1;
              }
            });
          }
        }
        this.snackbarService.showSuccess(isRate ? this.translateService.instant('PAYMENT_ENGINE.SHIPPING_ZONE.RATE_DELETED') : this.translateService.instant('PAYMENT_ENGINE.SHIPPING_ZONE.SHIPPING_OPTION_DELETED'));
      });
    }
    else
    {
      shippingRate.reset();
    }
  }


  deleteZone()
  {
    const dialogData: DialogData = {
      title: 'Delete',
      message: `Delete the ${ this.model.ShippingZone.ZoneName } Shipping Zone?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    if (this.zoneID)
    {
      this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
      {
        if (result)
        {
          const request = new DeleteShippingZoneRequest();
          request.ZoneID = this.zoneID;

          this.ShippingZoneService.Delete(request).subscribe(response =>
          {
            this.snackbarService.showSuccess(`${this.translateService.instant('PAYMENT_ENGINE.SHIPPING_ZONE.SHIPPING_ZONE')} ${ this.model.ShippingZone.ZoneName } ${this.translateService.instant('PAYMENT_ENGINE.SHIPPING_ZONE.DELETED')}.`);
            setTimeout(() =>
            {
              window.location.href = localStorage.getItem('viewShippingPage');
            }, 100);

          });
        }
      });
    }
  }

  goBack()
  {
    window.location.href = localStorage.getItem('viewShippingPage');
  }

  drop(event: CdkDragDrop<string[]>)
  {
    setRemoveTempArray(true);
    let shippingRate = this.form.get('Rates') as UntypedFormArray;
    moveItemInArray(shippingRate.controls, event.previousIndex, event.currentIndex);
    var index = 1;
    shippingRate.controls.forEach((x, i) =>
    {
      if (x.value.IsOption == true && x.value.IsDeleted == false)
      {
        x.value.RateIndex = index++;
      }
    });
    setRemoveTempArray(true);
  }
}
