import { UntypedFormControl, UntypedFormGroup, Validators } from '@angular/forms';
import { SelectedCreditCard, SelectedECheckAccount } from '@tei/common-payment-engine-shared';
import { GetPreAuthorizedPaymentResponse } from './get-pre-authorized-payment.model';

export class PostPreAuthorizedPaymentRequest
{
  public AccountID: string;
  public PreAuthPayType: string;
  public SelectedCreditCard: SelectedCreditCard;
  public SelectedECheckAccount: SelectedECheckAccount;
}

export class PostPreAuthorizedPaymentRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      AccountID: new UntypedFormControl(''),
      PreAuthPayType: new UntypedFormControl('', Validators.required),
      SelectedCreditCard: new UntypedFormControl(''),
      SelectedECheckAccount: new UntypedFormControl(''),
      
      AllowEcheckPaymentMethodForPreAuthStatements: new UntypedFormControl(''),
      AllowCreditCardPaymentMethodForPreAuthStatements: new UntypedFormControl(''),
      AllowCashEQPaymentMethodForPreAuthStatements: new UntypedFormControl(''),
      CCMsg: new UntypedFormControl(''),
      ECMsg: new UntypedFormControl(''),
      AllAccounts: new UntypedFormControl(''),
      ECheckAccounts: new UntypedFormControl(''),
      SelectedCreditCardAccounts: new UntypedFormControl('')
    });
  }

  public value: PostPreAuthorizedPaymentRequest;

  public setValue(value: GetPreAuthorizedPaymentResponse)
  {
    super.setValue(value);
    this.get('PreAuthPayType').valueChanges.subscribe(value =>
    {
      const authPayType = value as string;
      switch (authPayType)
      {
        case "CC":
          this.get('SelectedECheckAccount').clearValidators();
          this.get('SelectedECheckAccount').setValue(null);
          this.get('SelectedCreditCard').setValidators([Validators.required]);
          this.get('SelectedCreditCard').updateValueAndValidity();
          break;
        case "EC":
          this.get('SelectedCreditCard').clearValidators();
          this.get('SelectedCreditCard').setValue(null);
          this.get('SelectedECheckAccount').setValidators([Validators.required]);
          this.get('SelectedECheckAccount').updateValueAndValidity();
          break;
        default:
          this.get('SelectedECheckAccount').clearValidators();
          this.get('SelectedECheckAccount').setValue(null);
          this.get('SelectedCreditCard').clearValidators();
          this.get('SelectedCreditCard').setValue(null);
          break;
      }
    });
  }

}

export class PostPreAuthorizedPaymentResponse
{
  // public PreAuthPayType: string;
  // public SelectedCreditCard: SelectedCreditCard;
  // public SelectedECheckAccount: SelectedECheckAccount;
  public IsError: boolean;
  public ErrorMessage: string;
  public SucessMessage: string;
}
