import { DragDropModule } from '@angular/cdk/drag-drop';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { SearchWithChipModule, SnackBarModule } from '@tei/material';
import { provideEnvironmentNgxMask } from 'ngx-mask';
import { ShippingZoneService } from '../../@services/shipping-zone.service';
import { ShippingZoneComponent } from './shipping-zone.component';

const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [DragDropModule, MatDividerModule, MatListModule, MatCheckboxModule, MatCardModule, MatButtonModule, MatInputModule, MatSelectModule, MatExpansionModule, MatIconModule];
const TEI_MATERIAL_MODULES = [SearchWithChipModule, SnackBarModule];



@NgModule({
  declarations: [ShippingZoneComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: ShippingZoneComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  providers: [{ provide: 'IShippingZoneService', useClass: ShippingZoneService, }, provideEnvironmentNgxMask()],
})
export class ShippingZoneModule { }
