import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CreditCardDetail, ECheckAccount, SelectedCreditCard, SelectedECheckAccount } from '@tei/common-payment-engine-shared';
import { IAPIBaseUrlProvider } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { GetPreAuthorizedPaymentRequest, GetPreAuthorizedPaymentResponse, IPreAuthorizedPaymentService, PostPreAuthorizedPaymentRequestForm } from '../../@intermediate/pre-authorized-payment';

@Component({
  selector: 'tei-payment-engine-pages-pre-authorized-payment',
  templateUrl: './pre-authorized-payment.component.html',
  styleUrls: ['./pre-authorized-payment.component.scss']
})
export class PreAuthorizedPaymentComponent implements OnInit
{
  model: GetPreAuthorizedPaymentResponse;
  formPreAuthorizedPayment: PostPreAuthorizedPaymentRequestForm;
  accountID: string;

  constructor(@Inject('IPreAuthorizedPaymentService') private PreAuthorizedPaymentService: IPreAuthorizedPaymentService,
    private snackbarService: SnackBarService,
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    private activatedRoute: ActivatedRoute)
  {
    this.accountID = this.activatedRoute.snapshot.params['accountid'];
  }

  ngOnInit()
  {
    this.formPreAuthorizedPayment = new PostPreAuthorizedPaymentRequestForm();
    const request = new GetPreAuthorizedPaymentRequest();
    this.load(request);
    this.accountValueChanges();
    this.preAuthPayTypeChange();
  }



  private load(request: GetPreAuthorizedPaymentRequest)
  {
    if (!this.model)
    {
      this.model = new GetPreAuthorizedPaymentResponse();
    }

    this.PreAuthorizedPaymentService.Get(request).subscribe(response =>
    {
      this.model = response;
      response.AccountID = this.accountID;
      this.formPreAuthorizedPayment.setValue(response);

      // //Select the right type of radio button with the correct selected mode of payment
      // this.formPreAuthorizedPayment.controls.PreAuthPayType.valueChanges.subscribe(value =>
      // {
      //   if (this.model.PreAuthPayType === value && value === 'CC')
      //   {
      //     this.formPreAuthorizedPayment.controls.SelectedCreditCard.patchValue(this.model.SelectedCreditCard);
      //   }
      //   else if (this.model.PreAuthPayType === value && value === 'EC')
      //   {
      //     this.formPreAuthorizedPayment.controls.SelectedECheckAccount.patchValue(this.model.SelectedECheckAccount);
      //   }
      // });
    });
  }

  save(form: PostPreAuthorizedPaymentRequestForm)
  {
    this.PreAuthorizedPaymentService.Post(form.value).subscribe(response =>
    {
      if (response.IsError)
      {
        this.snackbarService.showError(response.ErrorMessage);
      }
      else
      {
        this.snackbarService.showSuccess("Saved successfully.");
        this.ngOnInit();
      }
    });

  }

  //On cancel always navigate back to the statements page.
  cancel()
  {
    window.location.href = "/web/my-account/statements";
  }

  preAuthPayTypeChange()
  {
    this.formPreAuthorizedPayment.controls.PreAuthPayType.valueChanges.subscribe(value =>
    {
      this.getSelectedPaymentByAccountID(value);
    });
  }

  getSelectedPaymentByAccountID(paymentType: string)
  {
    if (paymentType === "CC")
    {

      const selectedCard = this.model.SelectedCreditCardAccounts.find(f => f.AccountID == this.accountID);

      if (selectedCard)
      {
        let card = new SelectedCreditCard();
        card.SelectedCreditCardDetail = selectedCard as CreditCardDetail;
        this.formPreAuthorizedPayment.controls.SelectedCreditCard.patchValue(card);
        this.formPreAuthorizedPayment.controls.SelectedCreditCard.updateValueAndValidity();
      }
    }

    if (paymentType === "EC")
    {
      const selectedAccount = this.model.ECheckAccounts.find(f => f.AccountID == this.accountID);

      if (selectedAccount)
      {
        let eAccount = new SelectedECheckAccount();
        eAccount.SelectedECheckAccountDetail = selectedAccount as ECheckAccount;
        // this.model.SelectedECheckAccount.SelectedECheckAccountDetail = selectedAccount;
        this.formPreAuthorizedPayment.controls.SelectedECheckAccount.patchValue(eAccount);
        this.formPreAuthorizedPayment.controls.SelectedECheckAccount.updateValueAndValidity();
      }
    }
  }

  accountValueChanges()
  {
    this.formPreAuthorizedPayment.controls.AccountID.valueChanges.subscribe(value =>
    {
      let selectedValue = this.getPrePayaymentType(value);

      this.accountID = value;

      this.formPreAuthorizedPayment.controls.PreAuthPayType.patchValue("NA");

      setTimeout(() =>
      {
        if (!selectedValue)
        {
          selectedValue = "NA";
        }
        this.formPreAuthorizedPayment.controls.PreAuthPayType.patchValue(selectedValue);
      }, 100);
    });
  }

  private getPrePayaymentType(accountID: string)
  {
    return this.model.AllAccounts.find(s => s.AccountID == accountID).PreAuthPayType;
  }


}
