import { PickupSettings } from '../../pick-up-location-setting';

export class GetPickUpLocationRequest
{
  public LocationID: string;
}

export class GetPickUpLocationResponse
{
  public PickupSetting: PickupSettings;
  public Countries: Array<Country>;
}

export class Country
{
  public CountryCode: string;
  public CountryName: string;
  public CountryRegions: Array<Regions>;
}

export class Regions
{
  public CountryCode: string;
  public RegionCode: string;
  public RegionName: string;
}
