import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IShippingZoneService, GetShippingZoneRequest, GetShippingZoneResponse, PostShippingZoneRequest, PostShippingZoneResponse } from '../@intermediate/shipping-zone';
import { DeleteShippingZoneResponse, DeleteShippingZoneRequest } from '../@intermediate/shipping-zone/models/delete-shipping-zone.model';

@Injectable({
  providedIn: 'root'
})

export class ShippingZoneService implements IShippingZoneService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/ShippingZone';
  }

  public Get(request: GetShippingZoneRequest): Observable<GetShippingZoneResponse>
  {
    const apiUrl = this.serviceUrl + '/GetShippingZone';
    return this.http.get<GetShippingZoneResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostShippingZoneRequest): Observable<PostShippingZoneResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostShippingZoneResponse>(apiUrl, request);
  }

  public Delete(request: DeleteShippingZoneRequest): Observable<DeleteShippingZoneResponse>
  {
    const apiUrl = this.serviceUrl + "/DeleteShippingZone";
    return this.http.delete<DeleteShippingZoneResponse>(apiUrl, { params: request as any });
  }
}
