import { Component, OnInit, Inject } from '@angular/core';
import { IAcceptedShippingRegionService, GetAcceptedShippingRegionRequest, GetAcceptedShippingRegionResponse } from '../../@intermediate/accepted-shipping-region';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';

@Component({
  selector: 'tei-payment-engine-accepted-shipping-region',
  templateUrl: './accepted-shipping-region.component.html',
  styleUrls: ['./accepted-shipping-region.component.scss']
})
export class AcceptedShippingRegionComponent implements OnInit
{
  model: GetAcceptedShippingRegionResponse;

  constructor(@Inject('IAcceptedShippingRegionService') private AcceptedShippingRegionService: IAcceptedShippingRegionService, public dialog: MatDialog)
  {

  }

  ngOnInit()
  {
    const request = new GetAcceptedShippingRegionRequest();
    this.load(request);
  }

  private load(request: GetAcceptedShippingRegionRequest)
  {
    if (!this.model)
    {
      this.model = new GetAcceptedShippingRegionResponse();
    }

    this.AcceptedShippingRegionService.Get(request).subscribe(response =>
    {
      this.model = response;
    });
  }

  onNoClick(): void
  {
    this.dialog.closeAll();
  }
}
