import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IQuickPaymentService } from '../@intermediate/quick-payment/quick-payment.interface';
import { PostQuickPaymentCheckoutRequest, PostQuickPaymentCheckoutResponse } from '../@intermediate/quick-payment';

@Injectable({
  providedIn: 'root'
})

export class QuickPaymentService implements IQuickPaymentService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/QuickPayment';
  }

  public Post(request: PostQuickPaymentCheckoutRequest): Observable<PostQuickPaymentCheckoutResponse>
  {
    const apiUrl = this.serviceUrl + '/Checkout';
    return this.http.post<PostQuickPaymentCheckoutResponse>(apiUrl, request);
  }
}
