import { AfterViewInit, Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GetDefaultRequest, IDefaultService } from '../../@intermediate/default';

@Component({
  selector: 'tei-payment-engine-pages-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent implements OnInit, AfterViewInit
{
  sesssionid: string;

  constructor(@Inject('IDefaultService') private defaultService: IDefaultService,
    private route: ActivatedRoute,
    private router: Router)
  {
    this.route.params.subscribe(params =>
    {
      this.sesssionid = params.id;
    });
  }

  ngAfterViewInit(): void
  {
    const request = new GetDefaultRequest();
    this.load(request);
  }

  ngOnInit()
  {

  }

  //This will help to navigate to the checkout screen or the billing screen based on the setting in the settings
  private load(request: GetDefaultRequest)
  {
    this.defaultService.Get(request).subscribe(response =>
    {
      var navigator = response.NavigationPage + (this.sesssionid ? '\\' + this.sesssionid : '');
      this.router.navigate([navigator]);
    });
  }

}
