import { NgModule } from '@angular/core';
import { PickUpLocationComponent } from './pick-up-location.component';
import { PickUpLocationService } from '../../@services/pick-up-location.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { CommonModule } from '@angular/common';
import { SnackBarModule } from '@tei/material';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';

const CORE_MODULES = [TeiTextTranslatePipeModule,TranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatFormFieldModule, MatSelectModule, MatInputModule, MatCardModule, MatButtonModule, MatCheckboxModule];
const TEI_MATERIAL_MODULES = [SnackBarModule];

const COMPONENTS = [PickUpLocationComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IPickUpLocationService', useClass: PickUpLocationService }],
})
export class PickUpLocationModule { }
