import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { SelectedAddress } from '@tei/common-my-account-shared';
import { SelectedCreditCard, SelectedECheckAccount } from '@tei/common-payment-engine-shared';
import { PickupSettings } from '../../pick-up-location-setting';
import { GetBillingResponse } from './get-billing.model';
import { LocalStorageEventItem } from './localstorageeventitem.model';
import { Payment } from './payment.model';

export class PostBillingRequest
{
  public paymentDueNow: Payment;
  public sessionId: string;
  public dueAtCourse: boolean;
  public ShippingAddress: SelectedAddress;
  public PaymentMethodCC: SelectedCreditCard;
  public PaymentMethodEC: SelectedECheckAccount;
  public PaymentMethodAR: SelectedARAccount;
  public BillingAddress: SelectedAddress;
  public giftMessageNeeded: boolean;
  public giftMessage: string;
  public SaveCreditCard: boolean;
  public AvoidSurcharge: number;
  public SelectedShippingOption: string;
  public ShippingAddressMethod: number;
  public SelectedPickupDetail: PickupSettings[];
  public LocalStorageEventItems: Array<LocalStorageEventItem>;
  public QuickPaymentUID: string = null;
}

export class PostBillingRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      SelectedPickupDetail: new UntypedFormControl(''),
      ShippingAddress: new UntypedFormControl(''),
      PaymentMethodCC: new UntypedFormControl(''),
      PaymentMethodEC: new UntypedFormControl(''),
      PaymentMethodAR: new UntypedFormControl(''),
      BillingAddress: new UntypedFormControl(''),
      giftMessageNeeded: new UntypedFormControl(false),
      giftMessage: new UntypedFormControl(''),
      agreeTermConditions: new UntypedFormControl(''),
      SaveCreditCard: new UntypedFormControl(true),
      AvoidSurcharge: new UntypedFormControl(0),
      SelectedShippingOption: new UntypedFormControl(''),
      ShippingAddressMethod: new UntypedFormControl(''),
    });
  }

  public value: PostBillingRequest;

  public setValue(value: GetBillingResponse)
  {
    const request = new PostBillingRequest();
    request.ShippingAddress = value.ShippingAddress;
    request.PaymentMethodCC = value.PaymentMethodCC;
    request.PaymentMethodEC = value.PaymentMethodEC;
    request.PaymentMethodAR = value.PaymentMethodAR;
    request.BillingAddress = value.BillingAddress;
    request.giftMessageNeeded = value.giftMessageNeeded;
    request.giftMessage = value.giftMessage;
    request.SelectedShippingOption = value.SelectedShippingOption;
    request.ShippingAddressMethod = value.ShippingAddressMethod;
    request.SelectedPickupDetail = value.SelectedPickupDetail;
    super.setValue(request);
  }
}

export class PostBillingResponse
{
  public Approved: boolean;
  public CCType: string;
  public Response: string;
  public ResultMessage: string;
  public Result: string;
  public ReceiptHTML: string;
  public TransactionUID: string;
  public InvoiceID: string;
  public SSLTransactionID: string;
  public PaymentProcessorType: string;
  public QuickPaymentUID: string;
}

export class SelectedARAccount
{
  AccountID: string;
}
