import { NgModule } from '@angular/core';
import { AcceptedShippingRegionComponent } from './accepted-shipping-region.component';
import { AcceptedShippingRegionService } from '../../@services/accepted-shipping-region.service';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatDividerModule } from '@angular/material/divider';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatFormFieldModule, MatDividerModule, MatButtonModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [AcceptedShippingRegionComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IAcceptedShippingRegionService', useClass: AcceptedShippingRegionService }],
})
export class AcceptedShippingRegionModule { }
