import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyInputModule as MatInputModule } from '@angular/material/legacy-input';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { TeiCurrencyTranslateModule, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, ConfirmDialogModule, CountDownTimerModule, LoadingModule, SnackBarModule } from '@tei/material';
import { CheckoutService } from '../../@services/checkout.service';
import { CheckoutComponent } from './checkout.component';
import { ShoppingCartService } from '../../@services/shopping-cart.service';
// import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule, TeiCurrencyTranslateModule, TeiDateTranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatButtonModule, MatCardModule, MatIconModule, MatDividerModule, MatInputModule];
const TEI_MATERIAL_MODULES = [SnackBarModule, ConfirmDialogModule, CountDownTimerModule, LoadingModule, BreadCrumbModule];

@NgModule({
  declarations: [CheckoutComponent],
  imports: [
    ...CORE_MODULES,
    RouterModule.forChild([{ path: '', component: CheckoutComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  providers: [
    { provide: 'ICheckoutService', useClass: CheckoutService },
    { provide: 'IShoppingCartService', useClass: ShoppingCartService }
  ]
  ,
})
export class CheckoutModule { }
