import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetPickUpLocationRequest, GetPickUpLocationResponse, IPickUpLocationService, PostPickUpLocationRequest, PostPickUpLocationResponse } from '../@intermediate/pick-up-location';

@Injectable({
  providedIn: 'root'
})

export class PickUpLocationService implements IPickUpLocationService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/PickUpLocation';
  }

  public Get(request: GetPickUpLocationRequest): Observable<GetPickUpLocationResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetPickUpLocationResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostPickUpLocationRequest): Observable<PostPickUpLocationResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostPickUpLocationResponse>(apiUrl, request);
  }
}
