import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { PickUpLocationComponent } from './components/pick-up-location/pick-up-location.component';
import { BillingComponent } from './pages/billing/billing.component';
import { CheckoutComponent } from './pages/checkout/checkout.component';
import { ConfirmationComponent } from './pages/confirmation/confirmation.component';
import { DefaultComponent } from './pages/default/default.component';
import { FinalizePaymentComponent } from './pages/finalize-payment/finalize-payment.component';
import { PagesComponent } from './pages/pages.component';
import { PreAuthorizedPaymentComponent } from './pages/pre-authorized-payment/pre-authorized-payment.component';
import { QuickPaymentComponent } from './pages/quick-payment/quick-payment.component';
import { SettingsComponent } from './pages/settings/settings.component';
import { ShippingZoneComponent } from './pages/shipping-zone/shipping-zone.component';
//import { DynamicRouteProvider } from '@tei/core-dnn';

const pages_routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      {
        path: '',
        component: DefaultComponent,
        data: { RouteData: new RouteData({ Title: 'Payment Engine', ShowBottomToolbar: false }) }
      },
      {
        path: 'pre-authorized-statement-payment/:accountid',
        component: PreAuthorizedPaymentComponent,
        data: { RouteData: new RouteData({ Title: 'Pre-Authorized Statement Payment' }) }
      },
      {
        path: 'checkout',
        redirectTo: 'checkout/'
      },
      {
        path: 'checkout/:id',
        component: CheckoutComponent,
        data: { RouteData: new RouteData({ Title: 'Checkout', ShowBottomToolbar: false }) }
      },
      {
        path: 'checkout/:id/:accountID',
        component: CheckoutComponent,
        data: { RouteData: new RouteData({ Title: 'Checkout', ShowBottomToolbar: false }) }
      },
      {
        path: 'billing',
        redirectTo: 'billing/'
      },
      {
        path: 'billing/:id',
        component: BillingComponent,
        data: { RouteData: new RouteData({ Title: 'Billing', ShowBottomToolbar: false }) }
      },
      {
        path: 'billing/:id/:accountID',
        component: BillingComponent,
        data: { RouteData: new RouteData({ Title: 'Billing', ShowBottomToolbar: false }) }
      },
      {
        path: 'finalize-payment/:id',
        component: FinalizePaymentComponent,
        data: { RouteData: new RouteData({ Title: 'Finalize Payment', ShowBottomToolbar: false }) }
      },
      {
        path: 'settings',
        component: SettingsComponent,
        data: { RouteData: new RouteData({ Title: 'Settings' }) }
      },
      {
        path: 'confirmation/:id',
        component: ConfirmationComponent,
        data: { RouteData: new RouteData({ Title: 'Confirmation', ShowBottomToolbar: false }) }
      },
      {
        path: 'confirmation/:id/:quickPaymentUID',
        component: ConfirmationComponent,
        data: { RouteData: new RouteData({ Title: 'Confirmation', ShowBottomToolbar: false }) }
      },
      {
        path: 'shipping-zone/manage/:zone-id',
        component: ShippingZoneComponent,
        data: { RouteData: new RouteData({ Title: 'Shipping Zone', CanGoBack: false, ShowBottomToolbar: false }) }
      },
      {
        path: 'shipping-zone/manage',
        redirectTo: 'shipping-zone/manage/',
        data: { RouteData: new RouteData({ Title: 'Shipping Zone', CanGoBack: false }) }
      },
      {
        path: 'pick-up-location-setting/manage',
        component: PickUpLocationComponent,
        data: { RouteData: new RouteData({ Title: 'Pickup Location' }) }
      },
      {
        path: 'quick-payment/checkout',
        component: QuickPaymentComponent,
        data: { RouteData: new RouteData({ Title: 'QuickPayment' }) }
      },
    ]
  },
  { path: '**', redirectTo: '' },
];

// const pre_auth_routes: Routes = [
//   { path: '', loadChildren: () => import('./pre-auth/pre-authorized-payment/pre-authorized-payment.module').then(m => m.PreAuthorizedPaymentModule) },
//   { path: '**', redirectTo: '' },
// ];

@NgModule({
  imports: [
    RouterModule.forChild(pages_routes)
    //   DynamicRouteProvider.forChild({
    //   default: pages_routes
    //   // pre_auth: pre_auth_routes
    // })
  ],
  exports: [RouterModule],
})
export class PaymentEngineRoutingModule
{
}
