import { LocationStrategy } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormArray } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { Store, select } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';
import { IAuthService } from '@tei/abstraction';
import { IBookingState, SetBookingTimer, selectBookingState } from '@tei/booking-event';
import { IAPIBaseUrlProvider, IThemeState, selectSidebar } from '@tei/core';
import { ConfirmDialogService, DialogData, SnackBarService } from '@tei/material';
import { IShoppingCartItemsState, ResetShoppingCartAction, ShoppingCartService, selectShoppingCart } from '@tei/retail-ecommerce';
import { firstValueFrom } from 'rxjs';
import { LocalStorageItem } from '../../@intermediate/billing/models/localstorageitem.model';
import { CartItem, CartItemsRequest, GetCheckoutRequest, GetCheckoutResponse, ICheckoutService, PostCheckoutItemForm, PostCheckoutRequestForm } from '../../@intermediate/checkout';
import { IShoppingCartService } from '../../@intermediate/shopping-cart';

@Component({
  selector: 'tei-payment-engine-pages-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.scss']
})
export class CheckoutComponent implements OnInit
{
  model: GetCheckoutResponse; //Main model that holds all the data
  timerDuration: number;
  timeLeft: number;
  formCheckout: PostCheckoutRequestForm; //Main form
  sesssionid: string; //Session ID from the URL string
  isLoading: boolean;
  accountID: string | null;

  breadCrumbData = [
    {
      title: 'Cart',
      relativeUrl: ''
    }
  ];

  constructor(@Inject('ICheckoutService') private CheckoutService: ICheckoutService,
    private shoppingCartService: ShoppingCartService,
    private snackbarService: SnackBarService,
    private route: ActivatedRoute,
    private router: Router,
    private teiTextTranslateService: TranslateService,
    private translateService: TranslateService,
    private confirmdialogService: ConfirmDialogService,
    private location: LocationStrategy,
    private store: Store<IShoppingCartItemsState>,
    private bookingStore: Store<IBookingState>,
    private storeTheme: Store<IThemeState>,
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider,
    @Inject('IAuthService') private authService: IAuthService,
    @Inject('IShoppingCartService') private checkoutShoppingCartService: IShoppingCartService)
  {
    this.retrieveSessionID();

    this.preventBackButton();
  }

  private retrieveSessionID()
  {
    //Getting the session ID from the URL; this will be seen only in case of statement payments
    this.route.params.subscribe(params =>
    {
      this.sesssionid = params.id;
      this.accountID = params.accountID;
    });
  }

  private preventBackButton()
  {
    history.pushState(null, null, window.location.href);
    this.location.onPopState(() =>
    {
      history.pushState(null, null, window.location.href);
    });
  }

  async ngOnInit()
  {

    await this.updateAnonymousShoppingCart();
    this.formCheckout = new PostCheckoutRequestForm(this.CheckoutService);
    const request = new GetCheckoutRequest();
    request.SessionID = this.sesssionid ? this.sesssionid : '';


    setTimeout(() =>
    {
      this.setCheckoutTime(request);
      this.load(request);
    }, 100);

  }

  private async getBookingTimer()
  {
    const value = await firstValueFrom(this.bookingStore.select(selectBookingState));
    if (value?.timeLeft)
    {
      this.timerDuration = +value.timeLeft;
    }

  }

  private async updateAnonymousShoppingCart()
  {
    const authenticated = await firstValueFrom(this.authService.isAuthenticatedOrRefresh());
    if (authenticated && this.store.select(selectShoppingCart))
    {
      const value = await firstValueFrom(this.store.select(selectShoppingCart));

      if (value?.total > 0 && value?.items.length > 0)
      {
        let ecommerceCartItems = new CartItemsRequest();
        const cartItems: CartItem[] = value?.items as CartItem[];
        ecommerceCartItems.Item = cartItems;

        this.checkoutShoppingCartService.Post(ecommerceCartItems).subscribe(
          {
            next(value) { },
            error(err)
            {
              this.store?.dispatch(new ResetShoppingCartAction());
              this.snackbarService.showError(err.error.Title);
            },
            complete()
            {
              this.store?.dispatch(new ResetShoppingCartAction());
              setTimeout(() =>
              {
                this.shoppingCartService.onAddToShoppingCart();
              }, 100);

            },
          }
        );
      }
      else
      {
        this.shoppingCartService.onAddToShoppingCart();
      }
    }
    else
    {
      if (authenticated)
      {
        this.shoppingCartService.onAddToShoppingCart();
      }
    }

  }

  private async setCheckoutTime(request: GetCheckoutRequest)
  {
    if (localStorage.getItem('leftDetailTime'))
    {
      request.Timer = Number(localStorage.getItem('leftDetailTime')) / 60;
    }
    else if (this.bookingStore.select(selectBookingState))
    {
      await this.getBookingTimer();
      if (this.timerDuration)
        request.Timer = this.timerDuration / 60;
    }
  }

  private load(request: GetCheckoutRequest)
  {
    //Reading from local storage for ecommerce items only
    if (localStorage.getItem('jStorage') && localStorage.getItem('jStorage') != "{}")
    {
      const c = JSON.parse("[" + localStorage.getItem('jStorage') + "]");
      if (!request.LocalStorageItems)
      {
        request.LocalStorageItems = new Array<LocalStorageItem>();
      }
      c.forEach((element, index) =>
      {
        var tmp = new LocalStorageItem();
        let id = JSON.stringify(element).split(':', 1)[0].replace('{"', '').replace('"', '');

        if (id == "__jstorage_meta") // old estore
        {

          for (let entry of localStorage.getItem('jStorage').split(','))
          {
            if (entry.includes('"quantity"'))
            {
              var temp = entry.split(':');
              tmp.ItemID = temp[0].replace(/"/g, '');
              tmp.ItemType = "eCommerce Item";
              tmp.Quantity = Number(temp[2].replace(/}/g, ''));
              request.LocalStorageItems.push(tmp);
            }
          }
          return;
        }
        else
        {
          tmp.ItemID = element[id][2].itemId;
          tmp.Quantity = element[id][0].quantity;
          tmp.VariantID = element[id][1].variant;
          tmp.ItemType = "eCommerce Item";
          request.LocalStorageItems.push(tmp);
        }



      });

      localStorage.removeItem('jStorage');
    }

    if (!this.model)
    {
      this.model = new GetCheckoutResponse();
    }
    this.isLoading = true;
    this.CheckoutService.Get(request).subscribe(response =>
    {
      this.turnOffProgressBar();
      this.model = response;
      if (!this.timerDuration || this.timerDuration > this.model.TimerDuration)
        this.timerDuration = this.model.TimerDuration;
      this.formCheckout.setValue(response);

      //If there are no items for the checkout page then navigate to the previous page that payment engine was called from
      if (this.formCheckout.CheckoutItems.controls.length <= 0)
      {
        setTimeout(() =>
        {
          this.shoppingCartService.onAddToShoppingCart();
          this.GoBack();
        }, 1000);
      }
    }, error =>
    {
      this.turnOffProgressBar();
    });

    //If quantity is adjustable for an item, the total can be adjusted. Allowing only 99 in quantity
    this.formCheckout.get('CheckoutItems').valueChanges.subscribe(changes =>
    {
      changes.forEach(obj =>
      {
        if (obj.Quantity <= 0)
        {
          obj.Quantity = 1;
        }
        else if (obj.Quantity >= 100)
        {
          obj.Quantity = 99;
        }
        obj.Total = obj.Price * obj.Quantity;
      });
    });
  }

  leftTimerValue(timeLeft: number)
  {
    this.timeLeft = timeLeft;

  }

  turnOffProgressBar()
  {
    this.isLoading = false;
  }


  save(form: PostCheckoutRequestForm, navigateToBilling: boolean)
  {
    this.isLoading = true;
    this.CheckoutService.Post(form.value).subscribe(response =>
    {
      this.turnOffProgressBar();
      if (!response.IsError)
      {
        this.shoppingCartService.onAddToShoppingCart();
      }
      if (response.IsError)
      {
        this.snackbarService.showError(response.ErrorMessage);
      }
      else if (navigateToBilling)
      {
        if (this.accountID)
        {
          this.router.navigate(['payment-engine/billing/' + this.sesssionid + '/' + this.accountID]);
        }
        else
        {
          this.router.navigate(['payment-engine/billing/' + this.sesssionid]);
        }

        this.store.dispatch(new SetBookingTimer({ backUrl: '', timeLeft: this.timeLeft }));

      }
      else if (this.model.CheckoutItems.filter(x => x.ItemType === 'Tee Time Booking' || x.ItemType === ' "Tee Time CheckIn').length <= 0)
      {
        this.model.TimerDuration = 0;
      }

      //If there are no items for the checkout page then navigate to the previous page that payment engine was called from
      if (this.formCheckout.CheckoutItems.controls.length <= 0)
      {
        setTimeout(() =>
        {

          this.GoBack();
        }, 1000);
      }
    }, error =>
    {
      this.turnOffProgressBar();
    });
  }

  //Adjusting the quantity by using the +/- buttons next to quantity input
  adjustQuantity(by: number, m: PostCheckoutItemForm)
  {
    const val = m.controls.Quantity.value - by;
    if (val >= 1 && val <= 99)
    {
      m.controls.Quantity.patchValue(m.controls.Quantity.value - by);
    }
  }

  //Removing from cart real time with user interactive dialog
  deleteFromCart(m: PostCheckoutItemForm)
  {
    const dialogData: DialogData = {
      title: this.translateService.instant('PAYMENT_ENGINE.CHECKOUT.REMOVE'),
      message: this.translateService.instant('PAYMENT_ENGINE.CHECKOUT.ARE_YOU_SURE_YOU_WANY_TO_REMOVE') + m.controls.ItemName.value + this.translateService.instant('PAYMENT_ENGINE.CHECKOUT.FROM_YOUR_CART'),
      textOkayButton: this.translateService.instant('PAYMENT_ENGINE.CHECKOUT.REMOVE_'),
      textCancelButton: this.translateService.instant('PAYMENT_ENGINE.CHECKOUT.CANCEL'),
      buttonColor: 'warn'
    };

    const dialogRef = this.confirmdialogService.openDialog(dialogData, { disableClose: true, maxWidth: '600px' });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        const checkoutItemsFA = this.formCheckout.CheckoutItems as unknown as FormArray<PostCheckoutItemForm>;

        const cartId = m.CheckoutItem.CartID;
        const sameCartItems = checkoutItemsFA.controls.filter(
          (ctrl: PostCheckoutItemForm) => ctrl.CheckoutItem.CartID === cartId
        );

        if (sameCartItems.length > 1)
        {
          sameCartItems.forEach(ctrl =>
          {
            ctrl.controls.IsDeleted.patchValue(true);
            ctrl.CheckoutItem.IsDeleted = true;
          });
        } else
        {
          m.controls.IsDeleted.patchValue(true);
          m.CheckoutItem.IsDeleted = true;
        }

        this.save(this.formCheckout, false);

        if (sameCartItems.length > 1)
        {
          sameCartItems.forEach(ctrl =>
          {
            const formIndex = checkoutItemsFA.controls.indexOf(ctrl);
            if (formIndex > -1)
            {
              checkoutItemsFA.removeAt(formIndex);
            }
            const modelIndex = this.model.CheckoutItems.indexOf(ctrl.CheckoutItem);
            if (modelIndex > -1)
            {
              this.model.CheckoutItems.splice(modelIndex, 1);
            }
          });
        } else
        {
          const formIndex = checkoutItemsFA.controls.indexOf(m);
          if (formIndex > -1)
          {
            checkoutItemsFA.removeAt(formIndex);
          }
          const modelIndex = this.model.CheckoutItems.indexOf(m.CheckoutItem);
          if (modelIndex > -1)
          {
            this.model.CheckoutItems.splice(modelIndex, 1);
          }
        }

        this.snackbarService.showInfo(m.controls.ItemName.value + this.translateService.instant('PAYMENT_ENGINE.CHECKOUT.REMOVED_FROM_CART'));
      }
    });
  }

  //Calculated values for display on the screen
  get SubtotalValue()
  {
    var subtotal: number = 0;
    this.formCheckout.CheckoutItems.value.forEach(element =>
    {
      subtotal = subtotal + (!element.IsRewardStore && !element.IsDeleted ? element.Total : 0);
    });
    return subtotal;
  }
  get SubtotalRewardsValue()
  {
    var rewardSubtotal: number = 0;
    this.formCheckout.CheckoutItems.value.forEach(element =>
    {
      rewardSubtotal = rewardSubtotal + (element.IsRewardStore && !element.IsDeleted ? element.Total : 0);
    });
    return rewardSubtotal;
  }

  //Purging input values in the quantity input
  preventInput(m: PostCheckoutItemForm)
  {
    if (m.controls.Quantity.value >= 100)
    {
      m.controls.Quantity.patchValue(99);
    }
    else if (m.controls.Quantity.value < 0)
    {
      m.controls.Quantity.patchValue(1);
    }
  }

  //Timer control event trigger
  onTimesUp(event: boolean)
  {
    const dialogData: DialogData = {
      title: this.teiTextTranslateService.instant('PAYMENT_ENGINE.CHECKOUT.RESERVATION_REMOVED'),
      message: this.teiTextTranslateService.instant('PAYMENT_ENGINE.CHECKOUT.YOUR_RESERVATION_TIMER_HAS_EXPIRED_DIALOG'),
      textOkayButton: this.teiTextTranslateService.instant('PAYMENT_ENGINE.CHECKOUT.OK'),
      textCancelButton: ''
    };

    const dialogRef = this.confirmdialogService.openDialog(dialogData, { disableClose: true });

    dialogRef.afterClosed().subscribe(result =>
    {
      window.location.reload();
    });
  }

  //This will help navigate to the previous page which triggered the payment engine
  async GoBack()
  {
    this.RemoveCheckOutTime();

    var previousURL = "";
    if (localStorage.getItem('PaymentEngineReturnURL'))
    {
      previousURL = localStorage.getItem('PaymentEngineReturnURL');
      const sideState = await firstValueFrom(this.storeTheme.pipe(select(selectSidebar)));
      if (sideState.Identifier === 'web-my-account')
      {
        const ItineraryUrl = localStorage.getItem("ItineraryURL");
        // window.location.href = ItineraryUrl;
        const path = new URL(ItineraryUrl)?.pathname;
        this.router.navigate(
          [path],
          { replaceUrl: true }
        ).then(() => { localStorage.removeItem('PaymentEngineReturnURL'); });
        return;
      }

      if (previousURL.indexOf('?') > 0)
      {
        previousURL = previousURL.substring(0, previousURL.indexOf('?'));
      }
      else if (previousURL.toLowerCase().indexOf('/view/') > 0)
      {
        previousURL = previousURL.substring(0, previousURL.toLowerCase().indexOf('/view/'));
      }

      previousURL = this.convertToUrl(previousURL);

      this.router.navigateByUrl(previousURL).then(() => { localStorage.removeItem('PaymentEngineReturnURL'); });
    }
    else
    {
      // window.location.href = window.location.origin;
      if (localStorage.getItem('checkout-backurl'))
      {
        let backURL = localStorage.getItem('checkout-backurl');
        console.log(backURL);
        localStorage.removeItem('checkout-backurl');
        this.router.navigate([backURL]);
      } else
      {
        console.log([this.model.TeeTimeURL]);
        this.router.navigate([this.model.TeeTimeURL]);
      }
    }
  }

  convertToUrl(previousURL: string): string
  {
    try
    {
      const url = new URL(previousURL);
      return url.pathname;
    } catch
    {
      return previousURL;
    }
  }

  private RemoveCheckOutTime()
  {
    localStorage.removeItem('leftDetailTime');
  }
}
