import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { ICheckoutService } from '../checkout.interface';
import { CheckoutItem, GetCheckoutResponse } from './get-checkout.model';


export class PostCheckoutRequest
{
  public CheckoutItems: CheckoutItem[];
}

class PostCheckoutRequestArray extends UntypedFormArray
{

  constructor(private CheckoutService: ICheckoutService)
  {
    super([]);
  }

  public setValue(checkoutItems: CheckoutItem[])
  {
    this.clear();

    checkoutItems.forEach(checkoutItem =>
    {
      const checkoutItemControl = new PostCheckoutItemForm(checkoutItem);
      this.push(checkoutItemControl);
    });
  }
}

export class PostCheckoutItemForm extends UntypedFormGroup
{
  public CheckoutItem: CheckoutItem;

  constructor(checkoutItem: CheckoutItem)
  {
    super({
      CartID: new UntypedFormControl(checkoutItem.CartID),
      CartItemID: new UntypedFormControl(checkoutItem.CartItemID),
      ItemName: new UntypedFormControl(checkoutItem.ItemName),
      ItemType: new UntypedFormControl(checkoutItem.ItemType),
      Price: new UntypedFormControl(checkoutItem.Price),
      Quantity: new UntypedFormControl(checkoutItem.Quantity),
      Total: new UntypedFormControl(checkoutItem.Total),
      IsDeleted: new UntypedFormControl(checkoutItem.IsDeleted),
      CanEdit: new UntypedFormControl(checkoutItem.CanEdit),
      IsRewardStore: new UntypedFormControl(checkoutItem.IsRewardStore),
      //RewardPointsEnabled: new FormControl(checkoutItem.RewardPointsEnabled),
      Details: new UntypedFormControl(checkoutItem.Details),
      NumberOfPlayers: new UntypedFormControl(checkoutItem.NumberOfPlayers),
      CourseName: new UntypedFormControl(checkoutItem.CourseName)
    });
    this.CheckoutItem = checkoutItem;
  }
}

export class PostCheckoutRequestForm extends UntypedFormGroup
{
  public CheckoutItems: PostCheckoutRequestArray;
  constructor(CheckoutService: ICheckoutService)
  {
    super({
      CheckoutItems: new PostCheckoutRequestArray(CheckoutService)
    });
    this.CheckoutItems = this.get('CheckoutItems') as PostCheckoutRequestArray;
  }
  public value: PostCheckoutRequest;
  public setValue(value: GetCheckoutResponse)
  {
    const request = new PostCheckoutRequest();
    this.CheckoutItems.setValue(value.CheckoutItems);
    request.CheckoutItems = value.CheckoutItems;
    super.setValue(request);
  }
}

export class PostCheckoutResponse
{
  public IsError: boolean;
  public ErrorMessage: string;
}

export class CartItemsRequest
{
  Item: CartItem[];
}

export class CartItem
{
  eCommItemID: string;
  CashEQItemID: string;
  ItemID: number;
  ItemType: string;
  Quantity: number;
  ItemCode: string;
  RecipientEmail: string;
  GiftTo: string;
  GiftFrom: string;
  GiftMessage: string;
  EmailDeliveryDateUtc: Date;
  EmailDeliveryTimeUtc: Date;
  EmailSendOnUtc: Date;
  DenominationID: number;
  DenominationAmount: number;
  CashEQID: string;
}