import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { MatLegacySlideToggleModule as MatSlideToggleModule } from '@angular/material/legacy-slide-toggle';
import { MatLegacyTabsModule as MatTabsModule } from '@angular/material/legacy-tabs';
import { RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { MyAccountSharedModule } from '@tei/common-my-account-shared';
import { CashEqRedemptionModule, SelectCreditCardModule, SelectECheckAccountModule } from "@tei/common-payment-engine-shared";
import { TeiCurrencyTranslateModule, TeiCurrencyTranslatePipe, TeiDateTranslateModule, TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BreadCrumbModule, ConfirmDialogModule, CountDownTimerModule, LoadingModule } from '@tei/material';
import { BillingService } from '../../@services/billing.service';
import { AcceptedShippingRegionModule } from '../../components/accepted-shipping-region/accepted-shipping-region.module';
import { BillingTermsConditionsDialogModule } from '../../components/billing-terms-conditions-dialog/billing-terms-conditions-dialog.module';
import { BillingComponent } from './billing.component';

const CORE_MODULES = [TeiTextTranslatePipeModule, TeiCurrencyTranslateModule, TranslateModule, TeiDateTranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatButtonModule, MatIconModule, MatCardModule, MatDividerModule, MatExpansionModule, MatSlideToggleModule, MatCheckboxModule, MatTabsModule, MatSlideToggleModule, MatDialogModule, MatRadioModule];
const TEI_MATERIAL_MODULES = [SelectCreditCardModule, MyAccountSharedModule, ConfirmDialogModule, SelectECheckAccountModule, AcceptedShippingRegionModule, CashEqRedemptionModule,
  CountDownTimerModule, LoadingModule, BreadCrumbModule];
const PAYMENT_ENGINE_MODULES = [BillingTermsConditionsDialogModule];

@NgModule({
  declarations: [BillingComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: BillingComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...PAYMENT_ENGINE_MODULES,
    ...CORE_MODULES
  ],
  providers: [{ provide: 'IBillingService', useClass: BillingService }, TeiCurrencyTranslatePipe]
})
export class BillingModule { }
