import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IBillingService, GetBillingRequest, GetBillingResponse, PostBillingRequest, PostBillingResponse } from '../@intermediate/billing';
import { PostLocalStorageBillingRequest, PostLocalStorageBillingResponse } from '../@intermediate/billing/models/post-local-storage-billing.model';

@Injectable({
  providedIn: 'root'
})

export class BillingService implements IBillingService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/Billing';
  }

  public Get(request: GetBillingRequest): Observable<GetBillingResponse>
  {
    const apiUrl = this.serviceUrl + '';
    //return this.http.get<GetBillingResponse>(apiUrl, { params: request as any });
    return this.http.post<GetBillingResponse>(apiUrl, request);
  }

  public Post(request: PostBillingRequest): Observable<PostBillingResponse>
  {
    const apiUrl = this.serviceUrl + '/PostBilling';
    return this.http.post<PostBillingResponse>(apiUrl, request);
  }

  public PostLocalStorage(request: PostLocalStorageBillingRequest): Observable<PostLocalStorageBillingResponse>
  {
    const apiUrl = this.serviceUrl + '/PostLocalStorage';
    return this.http.post<PostBillingResponse>(apiUrl, request);
  }
}
