import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetFinalizePaymentRequest, GetFinalizePaymentResponse, IFinalizePaymentService, PostFinalizePaymentRequest, PostFinalizePaymentResponse } from '../@intermediate/finalize-payment';

@Injectable({
  providedIn: 'root'
})

export class FinalizePaymentService implements IFinalizePaymentService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/FinalizePayment';
  }

  public Get(request: GetFinalizePaymentRequest): Observable<GetFinalizePaymentResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetFinalizePaymentResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostFinalizePaymentRequest): Observable<PostFinalizePaymentResponse>
  {
    const apiUrl = this.serviceUrl + '/PostPaymentFinalized';
    return this.http.post<PostFinalizePaymentResponse>(apiUrl, request);
  }
}
