import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { SnackBarService } from '@tei/material';
import { GetFinalizePaymentRequest, GetFinalizePaymentResponse, IFinalizePaymentService, PostFinalizePaymentRequest, PostFinalizePaymentRequestForm } from '../../@intermediate/finalize-payment';

@Component({
  selector: 'tei-payment-engine-finalize-payment',
  templateUrl: './finalize-payment.component.html',
  styleUrls: ['./finalize-payment.component.css']
})
export class FinalizePaymentComponent implements OnInit
{

  isLoading: boolean;
  sessionid: string; //Session ID from the URL string
  orderId: string; //Noon Order ID returned
  model: GetFinalizePaymentResponse;
  formFinalize: PostFinalizePaymentRequestForm;
  inputLocationId: string;
  deleteLocationId: string;
  constructor(@Inject('IFinalizePaymentService') private FinalizePaymentService: IFinalizePaymentService,
    private translateService: TranslateService,
    private route: ActivatedRoute,
    private router: Router,
    private snackbarService: SnackBarService)
  {
    this.route.params.subscribe(params =>
    {
      this.sessionid = params.id;
    });

    this.route.queryParams.subscribe(qa =>
    {
      this.orderId = qa.orderId;
    });

  }

  ngOnInit()
  {
    this.formFinalize = new PostFinalizePaymentRequestForm();
    const request = new GetFinalizePaymentRequest();

    this.load(request);
  }
  PostMessageAndGoBack(message: string)
  {
    this.snackbarService.showError(message);
    setTimeout(() =>
    {
      //localStorage.removeItem('leftDetailTime');
      var previousURL = "";
      if (localStorage.getItem('PaymentEngineReturnURL'))
      {
        var previousURL = localStorage.getItem('PaymentEngineReturnURL');
        localStorage.removeItem('PaymentEngineReturnURL');
        if (previousURL.indexOf('?') > 0)
        {
          window.location.href = previousURL.substring(0, previousURL.indexOf('?'));
        }
        else if (previousURL.toLowerCase().indexOf('/view/') > 0)
        {
          window.location.href = previousURL.substring(0, previousURL.toLowerCase().indexOf('/view/'));
        }
        else
        {
          window.location.href = previousURL;
        }
      }
    }, 3000); // 3000 milliseconds delay
  }

  private load(request: GetFinalizePaymentRequest)
  {
    this.isLoading = true;
    if (!this.model)
    {
      this.model = new GetFinalizePaymentResponse();
    }

    let referrer = null;
    const appDomain = window.location.host;
    if (document.referrer)
    {
      referrer = new URL(document.referrer);
    }
    if (referrer != null && referrer.host && referrer.host !== appDomain)
    {
      //check if it is noon payment response and post payment
      console.log("The visitor came from an outside source." + referrer.host);
      if (this.orderId)
      {
        //Post Finalize Payment
        const finalize = new PostFinalizePaymentRequest();
        finalize.orderId = this.orderId;
        finalize.sessionId = this.sessionid;

        this.FinalizePaymentService.Post(finalize).subscribe(response =>
        {
          this.isLoading = false;
          if (response.Approved) //Aprroved billing response
          {
            this.router.navigate(['payment-engine/confirmation/' + response.TransactionUID]);
          }
          else
          {
            if (response.Message != "")
            {
              this.PostMessageAndGoBack(response.Message);
            }
          }
        }, error =>
        {
          if (error.error)
          {
            this.PostMessageAndGoBack(error.error);
          }
        }
        );
      }
    } else
    {
      this.PostMessageAndGoBack("The visitor navigated from within the site or directly accessed the site.");
    }


  }

}
