import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IPreAuthorizedPaymentService, GetPreAuthorizedPaymentRequest, GetPreAuthorizedPaymentResponse, PostPreAuthorizedPaymentRequest, PostPreAuthorizedPaymentResponse } from '../@intermediate/pre-authorized-payment';

@Injectable({
  providedIn: 'root'
})

export class PreAuthorizedPaymentService implements IPreAuthorizedPaymentService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/PreAuthorizedPayment';
  }

  public Get(request: GetPreAuthorizedPaymentRequest): Observable<GetPreAuthorizedPaymentResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetPreAuthorizedPaymentResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostPreAuthorizedPaymentRequest): Observable<PostPreAuthorizedPaymentResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostPreAuthorizedPaymentResponse>(apiUrl, request);
  }
}
