import { LocalStorageItem } from '../../billing/models/localstorageitem.model';

export class GetCheckoutRequest
{
  public SessionID: string;
  public LocalStorageItems: Array<LocalStorageItem>;
  public Timer: number;
}

export class GetCheckoutResponse
{
  public CheckoutItems: CheckoutItem[];
  public RewardPointsEnabled: boolean;
  public TimerDuration: number;
  public TeeTimeURL: string;
}

export class CheckoutItem
{
  public CartID: string;
  public CartItemID: string;
  public ItemName: string;
  public ItemType: string;
  public Price: string;
  public Quantity: string;
  public Total: string;
  public IsDeleted: boolean;
  public CanEdit: boolean;
  public IsRewardStore: boolean;
  public Details: string;
  public CreatedDate: string;
  public CourseName: string;
  public NumberOfPlayers: string;
}
