import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetPickUpLocationSettingRequest, GetPickUpLocationSettingResponse, IPickUpLocationSettingService, PostPickUpLocationSettingRequest, PostPickUpLocationSettingResponse } from '../@intermediate/pick-up-location-setting';

@Injectable({
  providedIn: 'root'
})

export class PickUpLocationSettingService implements IPickUpLocationSettingService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/PickUpLocationSetting';
  }

  public Get(request: GetPickUpLocationSettingRequest): Observable<GetPickUpLocationSettingResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetPickUpLocationSettingResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostPickUpLocationSettingRequest): Observable<PostPickUpLocationSettingResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostPickUpLocationSettingResponse>(apiUrl, request);
  }
}
