import { Component, OnInit, Inject } from '@angular/core';
import { IPickUpLocationSettingService, GetPickUpLocationSettingRequest, GetPickUpLocationSettingResponse, PostPickUpLocationSettingRequest, PostPickUpLocationSettingRequestForm, PostPickUpLocationSettingResponse, PickupSettings } from '../../@intermediate/pick-up-location-setting';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { PickUpLocationComponent } from '../../components/pick-up-location/pick-up-location.component';
import { ConfirmDialogService, DialogData } from '@tei/material';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { SnackBarService } from '@tei/material';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'tei-payment-engine-pages-pick-up-location-setting',
  templateUrl: './pick-up-location-setting.component.html',
  styleUrls: ['./pick-up-location-setting.component.scss']
})
export class PickUpLocationSettingComponent implements OnInit
{
  model: GetPickUpLocationSettingResponse;
  form: PostPickUpLocationSettingRequestForm;
  inputLocationId: string;
  deleteLocationId: string;
  fileNameDialogRef: MatDialogRef<PickUpLocationComponent>;
  newFileNameDialogRef: MatDialogRef<PickUpLocationComponent>;
  constructor(@Inject('IPickUpLocationSettingService') private PickUpLocationSettingService: IPickUpLocationSettingService,
   public dialog: MatDialog,
   private translateService: TranslateService,
   private confirmdialogService: ConfirmDialogService,
   private snackbarService: SnackBarService)
  {

  }

  ngOnInit()
  {
    this.form = new PostPickUpLocationSettingRequestForm();
    const request = new GetPickUpLocationSettingRequest();

    this.load(request);
  }

  private load(request: GetPickUpLocationSettingRequest)
  {
    if (!this.model)
    {
      this.model = new GetPickUpLocationSettingResponse();
    }

    this.PickUpLocationSettingService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.form.setValue(response);
    });
  }

  save(form: PostPickUpLocationSettingRequestForm)
  {

    const request = new PostPickUpLocationSettingRequest();

    request.PickUpLocations = this.model.PickupSettings;

    this.PickUpLocationSettingService.Post(request).subscribe(response =>
    {
      this.snackbarService.showSuccess( this.translateService.instant('PAYMENT_ENGINE.PICK_UP_LOCATION_SETTING.SAVED'));
    });
  }

  openEditDialog(m: PickupSettings): void
  {
    this.inputLocationId = m.LocationID;
    this.fileNameDialogRef = this.dialog.open(PickUpLocationComponent, {
      panelClass: '',
      maxWidth: '80vh',
      data: { pickUpLocationId: this.inputLocationId }
    });
    this.fileNameDialogRef.afterClosed().subscribe(result =>
    {
      const request = new GetPickUpLocationSettingRequest();
      this.load(request);
    });
  }

  openNewDialog(): void
  {
    this.fileNameDialogRef = this.dialog.open(PickUpLocationComponent, {
      panelClass: '',
      maxWidth: '80vh',
    });
    this.fileNameDialogRef.afterClosed().subscribe(result =>
    {
      const request = new GetPickUpLocationSettingRequest();
      this.load(request);
    });
  }

  deletePickUpLocation(d: PickupSettings): void
  {
    const index = this.model.PickupSettings.findIndex(x => x.LocationID == d.LocationID);
    const dialogData: DialogData = {
      title: 'Delete',
      message: `Are you sure you want to delete ${ d.LocationName }?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };
    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        this.model.PickupSettings.forEach((x, i) =>
        {
          if (i == index)
          {
            x.IsDeleted = true;
          }
        });
        this.save(this.form);
        //this.snackbarService.showSuccess(`${ d.LocationName } deleted.`);
      }
    });
  }

  drop(event: CdkDragDrop<string[]>)
  {
    let pickUpSetting = this.model.PickupSettings;
    moveItemInArray(pickUpSetting, event.previousIndex, event.currentIndex);
    var index = 1;
    pickUpSetting.forEach((x, i) =>
    {
      x.SortOrder = index++;
    });
  }

  goBack()
  {
    window.location.href = localStorage.getItem('viewPickupPage');
  }
}
