import { UntypedFormGroup, UntypedFormControl } from '@angular/forms';
import { GetPickUpLocationResponse } from './get-pick-up-location.model';

export class PostPickUpLocationRequest
{
  public LocationID: string;
  public LocationName: string;
  public Address1: string;
  public City: string;
  public CountryCode: string;
  public RegionCode: string;
  public Country: string;
  public Region: string;
  public PostalCode: string;
  public IsEnabled: boolean;
}

export class PostPickUpLocationRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      LocationID: new UntypedFormControl(''),
      LocationName: new UntypedFormControl(''),
      Address1: new UntypedFormControl(''),
      City: new UntypedFormControl(''),
      CountryCode: new UntypedFormControl(''),
      RegionCode: new UntypedFormControl(''),
      Country: new UntypedFormControl(''),
      Region: new UntypedFormControl(''),
      PostalCode: new UntypedFormControl(''),
      IsEnabled: new UntypedFormControl('')
    });
  }

  public value: PostPickUpLocationRequest;

  public setValue(
    value: GetPickUpLocationResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostPickUpLocationRequest();

    request.LocationID = value.PickupSetting == null ? "" : value.PickupSetting?.LocationID;
    request.IsEnabled = value.PickupSetting == null ? false : value.PickupSetting?.IsEnabled;
    request.LocationName = value.PickupSetting == null ? "" : value.PickupSetting?.LocationName;
    request.Address1 = value.PickupSetting == null ? "" : value.PickupSetting?.Address1;
    request.City = value.PickupSetting == null ? "" : value.PickupSetting?.City;
    request.CountryCode = value.PickupSetting == null ? "" : value.PickupSetting?.CountryCode;
    request.RegionCode = value.PickupSetting == null ? "" : value.PickupSetting?.RegionCode;
    request.Region = value.PickupSetting == null ? "" : value.PickupSetting?.Region;
    request.Country = value.PickupSetting == null ? "" : value.PickupSetting?.Country;
    request.PostalCode = value.PickupSetting == null ? "" : value.PickupSetting?.PostalCode;
    super.setValue(request, options);
  }
}

export class PostPickUpLocationResponse
{

}
