import { Component, OnInit, Inject, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { IPickUpLocationService, GetPickUpLocationRequest, GetPickUpLocationResponse, PostPickUpLocationRequest, PostPickUpLocationRequestForm, PostPickUpLocationResponse, Country, Regions } from '../../@intermediate/pick-up-location';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { MatLegacyDialog as MatDialog, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { SnackBarService } from '@tei/material';
import { TranslateService } from '@ngx-translate/core';


@Component({
  selector: 'tei-payment-engine-pick-up-location',
  templateUrl: './pick-up-location.component.html',
  styleUrls: ['./pick-up-location.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(PickUpLocationComponent)
  ]
})
export class PickUpLocationComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetPickUpLocationResponse;
  form: PostPickUpLocationRequestForm;
  countries: Array<Country>;
  regions: Array<Regions>;
  pickUpLocationId: string = "";

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IPickUpLocationService') private PickUpLocationService: IPickUpLocationService,
    public dialog: MatDialog,
    private translateService: TranslateService,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private snackbarService: SnackBarService)
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostPickUpLocationRequestForm();
    const request = new GetPickUpLocationRequest();
    request.LocationID = (this.data) ? this.data.pickUpLocationId : this.pickUpLocationId;
    this.load(request);

    this.form.get("CountryCode").valueChanges.subscribe(res =>
    {
      if (res)
      {
        this.countryChanged(res);
      }
    });
  }

  private load(request: GetPickUpLocationRequest)
  {
    if (!this.model)
    {
      this.model = new GetPickUpLocationResponse();
    }

    this.PickUpLocationService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.countries = this.model.Countries;
      this.form.setValue(this.model);
    });
  }

  countryChangeAction(event: any)
  {
    this.countryChanged(event.value);
  }

  countryChanged(value: string)
  {
    if (value !== "")
    {
      this.regions = new Array<Regions>();
      this.regions = this.countries.find(n => n.CountryCode === value).CountryRegions;
    }
  }

  onCancel(): void
  {
    this.dialog.closeAll();
  }

  save(form: PostPickUpLocationRequestForm)
  {
    const countryName = this.model.Countries.filter(x => x.CountryCode == form.value.CountryCode);
    const regionName = (this.regions == undefined) ? [] : this.regions.filter(x => x.RegionCode == form.value.RegionCode);
    (countryName.length > 0) ? this.form.get("Country").patchValue(countryName[0].CountryName) : this.form.get("Country").patchValue('');
    (regionName.length > 0) ? (this.form.get("Region").patchValue(regionName.length > 0 ? regionName[0].RegionName : "")) : this.form.get("Region").patchValue('');
    this.PickUpLocationService.Post(form.value).subscribe(response =>
    {
      this.dialog.closeAll();
      this.snackbarService.showSuccess(this.translateService.instant('PAYMENT_ENGINE.PICK_UP_LOCATION.LOCATION_SAVED'));
    });
  }

}
