import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { ManageCreditCardListModule, ManageCreditCardModule, ManageECheckAccountListModule } from '@tei/common-payment-engine-shared';
import { DefaultService } from '../../@services/default.service';
import { DefaultComponent } from './default.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];
const BASE_MODULES = [];
const MATERIAL_MODULES = [];
const TEI_MATERIAL_MODULES = [ManageCreditCardModule, ManageCreditCardListModule, ManageECheckAccountListModule];

@NgModule({
  declarations: [DefaultComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: DefaultComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...CORE_MODULES
    //...TEI_MATERIAL_MODULES
  ],
  providers: [{ provide: 'IDefaultService', useClass: DefaultService }],
})
export class DefaultModule { }
