import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { IAPIBaseUrlProvider } from '@tei/core';
import { SnackBarService } from '@tei/material';
import { GetQuickPaymentResponse, IQuickPaymentService, PostQuickPaymentCheckoutRequest } from '../../@intermediate/quick-payment';


@Component({
  selector: 'tei-payment-engine-pages-quick-payment',
  templateUrl: './quick-payment.component.html',
  styleUrls: ['./quick-payment.component.scss']
})
export class QuickPaymentComponent implements OnInit
{
  model: GetQuickPaymentResponse;
  quickPaymentUID: string;

  constructor(@Inject('IQuickPaymentService') private QuickPaymentService: IQuickPaymentService,
    private snackbarService: SnackBarService,
    private route: ActivatedRoute,
    private router: Router,
    @Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider)
  {
    this.route.queryParams.subscribe(qa =>
    {
      this.quickPaymentUID = qa.quickPaymentUID;
    });
  }

  ngOnInit()
  {
    if (this.quickPaymentUID !== null && this.quickPaymentUID !== "")
    {
      let request = new PostQuickPaymentCheckoutRequest();
      request.QuickPaymentUID = this.quickPaymentUID;
      this.save(request);
    }
  }

  save(request: PostQuickPaymentCheckoutRequest)
  {
    this.QuickPaymentService.Post(request)
      .subscribe(response =>
      {
        if (response.AddedToShoppingCart === true)
        {
          this.router.navigate(['payment-engine/billing/'], { queryParams: { quickPaymentUID: this.quickPaymentUID } });
        }
        else
        {

        }
      });
  }
}
