import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { ICheckoutService, GetCheckoutRequest, GetCheckoutResponse, PostCheckoutRequest, PostCheckoutResponse } from '../@intermediate/checkout';

@Injectable({
  providedIn: 'root'
})

export class CheckoutService implements ICheckoutService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/Checkout';
  }

  public Get(request: GetCheckoutRequest): Observable<GetCheckoutResponse>
  {
    const apiUrl = this.serviceUrl + '';
    //return this.http.get<GetCheckoutResponse>(apiUrl, { params: request as any });
    return this.http.post<GetCheckoutResponse>(apiUrl, request);
  }

  public Post(request: PostCheckoutRequest): Observable<PostCheckoutResponse>
  {
    const apiUrl = this.serviceUrl + '/PostCheckout';
    return this.http.post<PostCheckoutResponse>(apiUrl, request);
  }
}
