import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDividerModule } from '@angular/material/divider';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { BillingTermsConditionsDialogComponent } from './billing-terms-conditions-dialog.component';

const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const CORE_MODULES = [TeiTextTranslatePipeModule];
const MATERIAL_MODULES = [MatButtonModule, MatIconModule, MatDividerModule, MatDialogModule];
const TEI_MATERIAL_MODULES = [];

@NgModule({
  declarations: [BillingTermsConditionsDialogComponent],
  imports: [
    ...BASE_MODULES,
    ...CORE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ]
})
export class BillingTermsConditionsDialogModule { }
