import { NgModule } from '@angular/core';
import { PickUpLocationSettingComponent } from './pick-up-location-setting.component';
import { PickUpLocationSettingService } from '../../@services/pick-up-location-setting.service';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { PickUpLocationModule} from '../../components/pick-up-location/pick-up-location.module';
import { PickUpLocationComponent} from '../../components/pick-up-location/pick-up-location.component';
import { ConfirmDialogModule } from '@tei/material';
import { DragDropModule } from '@angular/cdk/drag-drop';
import { SnackBarModule } from '@tei/material';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';
import { TranslateModule } from '@ngx-translate/core';

const CORE_MODULES = [TeiTextTranslatePipeModule, TranslateModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatIconModule, MatDialogModule, DragDropModule, MatChipsModule];
const TEI_MATERIAL_MODULES = [PickUpLocationModule, ConfirmDialogModule, SnackBarModule];

@NgModule({
    declarations: [PickUpLocationSettingComponent],
    imports: [
        RouterModule.forChild([{ path: '', component: PickUpLocationSettingComponent }]),
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...CORE_MODULES
    ],
    providers: [{ provide: 'IPickUpLocationSettingService', useClass: PickUpLocationSettingService }]
})
export class PickUpLocationSettingModule { }
