import { Component, Inject, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// import { IAPIBaseUrlProvider } from '@tei/core';
import { GetConfirmationRequest, GetConfirmationResponse, IConfirmationService, PostConfirmationRequestForm } from '../../@intermediate/confirmation';
import { firstValueFrom } from 'rxjs';
import { IThemeState, selectSidebar } from '@tei/core';
import { select, Store } from '@ngrx/store';

@Component({
  selector: 'tei-payment-engine-pages-confirmation',
  templateUrl: './confirmation.component.html',
  styleUrls: ['./confirmation.component.scss']
})
export class ConfirmationComponent implements OnInit
{
  model: GetConfirmationResponse; //Main model that holds all the data
  form: PostConfirmationRequestForm; //Main form
  transactionuid: string; //TransactionUID from the URL string
  isLoading: boolean;
  quickPaymentUID: string = null;

  constructor(@Inject('IConfirmationService') private ConfirmationService: IConfirmationService,
    private route: ActivatedRoute,
    private router: Router,
    private store: Store<IThemeState>,
  )
  {
    //Getting the TransactionUID from the URL, so that the correct data can be pulled from the database
    this.route.params.subscribe(params =>
    {
      this.transactionuid = params.id;
    });

    // Anonymous Payment 
    this.route.params.subscribe(qa =>
    {
      this.quickPaymentUID = qa.quickPaymentUID;
    });
  }

  ngOnInit()
  {
    //Code needed for the back navigation to work correctly
    // if(!localStorage.getItem('PaymentEngineReturnURL') && !window.location.href.startsWith(this.context.UrlReferrer))
    // {
    //   localStorage.setItem('PaymentEngineReturnURL', this.context.UrlReferrer);
    // }

    this.form = new PostConfirmationRequestForm();
    const request = new GetConfirmationRequest();
    this.load(request);
  }

  private load(request: GetConfirmationRequest)
  {
    if (!this.model)
    {
      this.model = new GetConfirmationResponse();
    }
    request.TransactionUID = this.transactionuid ? this.transactionuid : '';
    request.QuickPaymentUID = this.quickPaymentUID;
    this.isLoading = true;
    this.ConfirmationService.Get(request).subscribe(response =>
    {
      this.turnOffProgressBar();
      this.model = response;
      this.form.setValue(response);
    }, error =>
    {
      this.turnOffProgressBar();
    });
  }

  turnOffProgressBar()
  {
    this.isLoading = false;
  }


  //Navigate to previous module from where the call to payment engine was made
  async GoBack()
  {
    var previousURL = "";
    if (localStorage.getItem('PaymentEngineReturnURL'))
    {
      const sideState = await firstValueFrom(this.store.pipe(select(selectSidebar)));
      if (sideState.Identifier === 'web-my-account')
      {
        let ItineraryUrl = localStorage.getItem("ItineraryURL");
        // window.location.href = ItineraryUrl;
        const path = new URL(ItineraryUrl)?.pathname;
        this.router.navigate(
          [path],
          { replaceUrl: true }
        ).then(() => { localStorage.removeItem('PaymentEngineReturnURL'); });
        return;
      }
      var previousURL = localStorage.getItem('PaymentEngineReturnURL');

      
      if (previousURL.indexOf('?') > 0)
      {
        previousURL = previousURL.substring(0, previousURL.indexOf('?'));
      }
      else if (previousURL.toLowerCase().indexOf('/view/') > 0)
      {
        previousURL = previousURL.substring(0, previousURL.toLowerCase().indexOf('/view/'));
      }

      previousURL = this.convertToUrl(previousURL);

      this.router.navigateByUrl(previousURL).then(() => { localStorage.removeItem('PaymentEngineReturnURL'); });
    }
    else
    {
      //window.location.href = window.location.origin;
      this.router.navigate([this.model.TeeTimeURL]);
    }
  }

  convertToUrl(previousURL: string): string
  {
    try
    {
      const url = new URL(previousURL);
      return url.pathname;
    } catch
    {
      return previousURL;
    }
  }

  //As screen changes, the expansion pnels needed adjusting so this is placed to help with that
  get needToCollapse()
  {
    const ret = window.innerWidth >= 800 && window.innerHeight >= 600;
    return ret;
  }
}
