import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyChipsModule as MatChipsModule } from '@angular/material/legacy-chips';
import { MatDividerModule } from '@angular/material/divider';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatIconModule } from '@angular/material/icon';
import { RouterModule } from '@angular/router';
import { LoadingModule } from '@tei/material';
import { FinalizePaymentService } from '../../@services/finalize-payment.service';
import { FinalizePaymentComponent } from './finalize-payment.component';
const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MatIconModule, MatExpansionModule, MatDividerModule, MatCardModule, MatChipsModule];
const TEI_MATERIAL_MODULES = [LoadingModule];

@NgModule({
  declarations: [FinalizePaymentComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: FinalizePaymentComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
  ],
  providers: [{ provide: 'IFinalizePaymentService', useClass: FinalizePaymentService }]
})
export class FinalizePaymentModule { }
