import { SelectedAddress } from '@tei/common-my-account-shared';
import { SelectedCreditCard, SelectedECheckAccount } from '@tei/common-payment-engine-shared';
import { PickupSettings } from '../../pick-up-location-setting';
import { ARAccounts, ShippingOption } from './billingitem.model';
import { LocalStorageItem } from './localstorageitem.model';
import { Payment } from './payment.model';
import { SelectedARAccount } from './post-billing.model';

export class GetBillingRequest
{
  public SessionID: string;
  public AvoidSurcharge: string;
  public LocalStorageItems: Array<LocalStorageItem>;
  public ShippingAddress: string;
  public SelectedShippingOption: string;
  public IsPickup: number;
  public Timer: number;
  public QuickPaymentUID: string;
  public PaymentResponseRedirect: string;
}

export class GetBillingResponse
{
  public ItemsDueNow: Payment;
  public ItemsDueLater: Payment;
  public ItemsWaitList: Payment;
  public ShippingAddress: SelectedAddress;
  public PaymentMethodCC: SelectedCreditCard;
  public PaymentMethodEC: SelectedECheckAccount;
  public PaymentMethodAR: SelectedARAccount;
  public BillingAddress: SelectedAddress;
  public giftMessageNeeded: boolean;
  public giftMessage: string;
  public termsAndCondition: string;
  public IsShippingRequired: boolean;
  public IsPickupRequired: boolean;
  public ShippingMessage: string;
  public IsCCRequired: boolean;
  public TimerDuration: number;
  public IsECRequired: boolean;
  public IsECNeedSurcharge: boolean;
  public ShippingOptions: Array<ShippingOption>;
  public SelectedShippingOption: string;
  public PickupAddress: Array<PickupSettings>;
  public ShippingAddressMethod: number;
  SelectedPickupDetail: PickupSettings[];
  public PayNowList: BillingItemDetailed[];
  public AllowCashEqRedemptions: string;
  public AllowCreditCards: boolean;
  public AllowRedemptions: boolean;
  public TeeTimeURL: string;
  public DisplayTermsAndConditions: boolean;
  public DisplayManageCard: boolean;
  public MerchantPhone: string;
  public MerchantEmail: string;
  public MerchantAddress: string;
  public DisplayBookedDateTime: string;
  public QuickPayment: boolean;
  public RequirePreAuthPaymentMethod: boolean;
  public QuickPaymentMessage: string;
  public HostedPaymentRedirect: string;
  public ARAccounts: Array<ARAccounts>;
  public AllowARAccount: boolean;
  public AuthenticateUser: boolean = false;
  public QuickPaymentAccountUID: string = null;
}

export class BillingItemDetailed
{
  public CartId: string;
  public ItemId: string;
  public CartItemID: string;
  public CartOwnerID: string;
  public ItemName: string;
  public Price: string;
  public ItemType: string;
  public ContactName: string;
  public Quantity: string;
  public TaxTotal: string;
  public BaseEntityID: string;
  public SpecialPrice: string;
  public PayNow: boolean;
  public CCRequired: boolean;
  public IsRewardStore: boolean;
  public CreatedDate: Date | string;
}
