import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyRadioModule as MatRadioModule } from '@angular/material/legacy-radio';
import { RouterModule } from '@angular/router';
import { SelectCreditCardModule, SelectECheckAccountModule } from '@tei/common-payment-engine-shared';
import { PreAuthorizedPaymentService } from '../../@services/pre-authorized-payment.service';
import { PreAuthorizedPaymentComponent } from './pre-authorized-payment.component';
import { TeiTextTranslatePipeModule } from '@tei/core-translate';

const CORE_MODULES = [TeiTextTranslatePipeModule];
const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatRadioModule, MatCardModule, MatDividerModule];
const TEI_MATERIAL_MODULES = [SelectCreditCardModule, SelectECheckAccountModule];

@NgModule({
  declarations: [PreAuthorizedPaymentComponent],
  imports: [
    RouterModule.forChild([{ path: '', component: PreAuthorizedPaymentComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...CORE_MODULES
  ],
  providers: [{ provide: 'IPreAuthorizedPaymentService', useClass: PreAuthorizedPaymentService }],
})
export class PreAuthorizedPaymentModule { }
