import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core';
import { Observable } from 'rxjs';
import { GetAcceptedShippingRegionRequest, GetAcceptedShippingRegionResponse, IAcceptedShippingRegionService } from '../@intermediate/accepted-shipping-region';

@Injectable({
  providedIn: 'root'
})

export class AcceptedShippingRegionService implements IAcceptedShippingRegionService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, protected http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/PaymentEngine/AcceptedShippingRegion';
  }

  public Get(request: GetAcceptedShippingRegionRequest): Observable<GetAcceptedShippingRegionResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetAcceptedShippingRegionResponse>(apiUrl, { params: request as any });
  }
}
